import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

class MeetupDetails extends Component {
  state = {
    details: ""
  };

  getMeetup() {
    let meetupId = this.props.match.params.id;
    axios
      .get(`http://localhost:3000/api/meetups/${meetupId}`)
      .then(response => {
        this.setState({ details: response.data }, () => {
          console.log(this.state);
        });
      })
      .catch(err => console.log(err));
  }
  onDelete() {
    let meetupId = this.state.details.id;
    axios
      .delete(`http://localhost:3000/api/meetups/${meetupId}`)
      .then(response => {
        this.props.history.push("/");
      })
      .catch(err => console.log(err));
  }

  componentWillMount() {
    this.getMeetup();
  }
  render() {
    const { name, city, address, id } = this.state.details;
    return (
      <div>
        <br />
        <Link to="/" className="btn grey">
          Back
        </Link>
        <h4>{name}</h4>
        <ul className="collection">
          <li className="collection-item">{city}</li>
          <li className="collection-item">{address}</li>
        </ul>
        <Link className="btn" to={`/meetups/edit/${id}`}>
          Edit
        </Link>

        <button onClick={this.onDelete.bind(this)} className="btn red right">
          Delete
        </button>
      </div>
    );
  }
}

export default MeetupDetails;
